    font-family: myfont;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2");
}
*{
    box-sizing: border-box;
    /* outline: 1px solid red; */
    --primary: #000000;
    --secondary: #1b232f;
    --font-primary: #f1eff1;
    --font-secondary: #888c90;
    --border: #404347;
}
body{
    height: 100vh;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template: 50px 1fr 1fr 1fr / 2.2fr 1fr;
    gap: 20px;
    padding: 15px;
    background-color: var(--primary);
    color: var(--font-primary);
    font-family: myfont , 'Times New Roman', Times, serif;
}

body>*{
    border-radius: 15px;
    padding: 30px;
}
body>*:not(.today-hero, .search, .error){
    background-color: var(--secondary);
}

.search{
    grid-area: 1 / 1 / 2 / 2;
    padding: 0;
    display: flex;
    position: relative;
}
.search>input{
    z-index: 1;
    height: 100%;
    width: calc(100% - 50px);
    background-color: var(--secondary);
    border: none;
    border-radius: 15px 0 0 15px;
    padding: 5px 15px;
    font-size: 17px;
    color: var(--font-primary);
    transition: width 1s, border-radius 1s;
}
.search>input:placeholder-shown{
    width: 100%;
    border-radius: 15px;
    transition: width 1s, border-radius 1s;
}
.search>input:focus{
    outline: none;
}
.search>input::placeholder{
    color:  #d6d6d6;
}
.search>input:focus::placeholder{
    color: var(--font-secondary);
}

.search>button{
    position: absolute;
    right: 0;
    height: 100%;
    width: 50px;
    border-radius: 0 15px 15px 0;
    z-index: 0;
    background-color: var(--border);
    border: none;
    cursor: pointer;
    transition: right 1s;
    padding: 10px;
}
.search>input:placeholder-shown + button {
    right: 50px;
    transition: right 1s;
    pointer-events: none;
}
.search>button:hover{
    border: 1px solid var(--primary);
}
.search>button:active{
    border: 2px solid var(--primary);
}



.today-hero{
    grid-area: 2 / 1 / 3 / 2;
    display: flex;
    justify-content: space-between;
}
.today-info{
    display: flex;
    flex-direction: column;
}
.today-temp{
    margin-top : auto;
}
.hero-logo{
    height: 100%;
}

.today-forecast{
    grid-area: 3 / 1 / 4 / 2;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.time-container{
    display: flex;
    flex: 1;
    align-items: center;
}
.time-card{
    flex: 1;
    text-align: center;
    border-right: 1px solid var(--border);
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    gap: 8px;
}
.time-card:last-child{
    border-right: none;
}
.time{
    color: var(--font-secondary);
    font-weight: 700;
}
.time-temp{
    font-size: 1.4rem;
    font-weight: 650;
}
.time-img{
    height: calc(min(80px, 10vw));
}
.time-img>img{
    height: 100%;
    width: 100%;
}

.today-condition{
    grid-area: 4 / 1 / 5 / 2;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.condition-container{
    flex: 1;
    display: grid;
    grid-template: 1fr 1fr / 1fr 1fr;
    gap: 10px;
}
.condition-title{
    color: var(--font-secondary);
    display: flex;
    align-items: center;
    gap: 10px;
}
.value{
    font-size: 1.4rem;
    font-weight: 700;
    margin-top: 3px;
    margin-left: 35px;
}
.condition-title>img{
    height: 25px;
    width: 25px;
}


.week{
    grid-area: 2 / 2 / 5 / 3;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.day-container{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.day-card{
    flex: 1;
    display: grid;
    grid-template: 1fr / 1fr 2fr 1fr;
    gap: 30px;
    border-bottom: 1px solid var(--border);
}
.day-card:last-child{
    border-bottom: none;
}
.status{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: auto;
    gap: 10px;
    font-weight: 600;
    font-size: 0.95rem;
}
.status>img{
    height: 60px;
}
.day{
    padding-left: 10%;
}
.day, .day-temp{
    display: flex;
    align-items: center;
    color: var(--font-secondary);
}
.day-temp>span:first-child{
    width: 49%;
    text-align: right;
    color: var(--font-primary);
    font-weight: 650;
}
.day-temp>span:last-child{
    width: 49%;
    font-size: 0.9rem;
}


.title{
    color: var(--font-secondary);
    font-weight: 700;
}
.city{
    font-size: 2rem;
    font-weight: 800;
}
.hero-info{
    font-size: 1rem;
    color: var(--font-secondary);
}
.today-temp{
    font-size: 3.4rem;
    font-weight: bold;
}

.error{
    background-color: var(--primary);
    grid-area: 2 / 1 / 5 / 3;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    gap: 10px;
    border-radius: 0;
    padding: 0 40px;
}
.error img{
    height: 10%;
}
.hidden{
    visibility: hidden;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,mBAAmB;IACnB,4DAA0D;AAC9D;AACA;IACI,sBAAsB;IACtB,4BAA4B;IAC5B,kBAAkB;IAClB,oBAAoB;IACpB,uBAAuB;IACvB,yBAAyB;IACzB,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,SAAS;IACT,UAAU;IACV,aAAa;IACb,2CAA2C;IAC3C,SAAS;IACT,aAAa;IACb,gCAAgC;IAChC,0BAA0B;IAC1B,qDAAqD;AACzD;;AAEA;IACI,mBAAmB;IACnB,aAAa;AACjB;AACA;IACI,kCAAkC;AACtC;;AAEA;IACI,wBAAwB;IACxB,UAAU;IACV,aAAa;IACb,kBAAkB;AACtB;AACA;IACI,UAAU;IACV,YAAY;IACZ,wBAAwB;IACxB,kCAAkC;IAClC,YAAY;IACZ,4BAA4B;IAC5B,iBAAiB;IACjB,eAAe;IACf,0BAA0B;IAC1B,sCAAsC;AAC1C;AACA;IACI,WAAW;IACX,mBAAmB;IACnB,sCAAsC;AAC1C;AACA;IACI,aAAa;AACjB;AACA;IACI,eAAe;AACnB;AACA;IACI,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,YAAY;IACZ,WAAW;IACX,4BAA4B;IAC5B,UAAU;IACV,+BAA+B;IAC/B,YAAY;IACZ,eAAe;IACf,oBAAoB;IACpB,aAAa;AACjB;AACA;IACI,WAAW;IACX,oBAAoB;IACpB,oBAAoB;AACxB;AACA;IACI,gCAAgC;AACpC;AACA;IACI,gCAAgC;AACpC;;;;AAIA;IACI,wBAAwB;IACxB,aAAa;IACb,8BAA8B;AAClC;AACA;IACI,aAAa;IACb,sBAAsB;AAC1B;AACA;IACI,iBAAiB;AACrB;AACA;IACI,YAAY;AAChB;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,aAAa;IACb,OAAO;IACP,mBAAmB;AACvB;AACA;IACI,OAAO;IACP,kBAAkB;IAClB,qCAAqC;IACrC,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;AACA;IACI,kBAAkB;AACtB;AACA;IACI,4BAA4B;IAC5B,gBAAgB;AACpB;AACA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;AACA;IACI,6BAA6B;AACjC;AACA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,OAAO;IACP,aAAa;IACb,gCAAgC;IAChC,SAAS;AACb;AACA;IACI,4BAA4B;IAC5B,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,YAAY;IACZ,WAAW;AACf;;;AAGA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,OAAO;IACP,aAAa;IACb,gCAAgC;IAChC,SAAS;IACT,sCAAsC;AAC1C;AACA;IACI,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,WAAW;IACX,SAAS;IACT,gBAAgB;IAChB,kBAAkB;AACtB;AACA;IACI,YAAY;AAChB;AACA;IACI,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,mB